<template>
	<view class="u-wrap">
		<view class="u-search-box">
			<view class="u-search-inner">
				<u-search placeholder="搜索商品" v-model="keyword" @custom="searchGoods" @clear="clearSeach"></u-search>
			</view>
		</view>
		<view class="u-menu-wrap">
			<scroll-view scroll-y scroll-with-animation class="u-tab-view menu-scroll-view" :scroll-top="scrollTop">
				<block v-for="(item,index) in categories">
					<view class="u-line-1 navTitle">{{item.name}}</view>
					<view v-for="child in item.children" :key="child.id" class="u-tab-item"
					:class="[current==child.id ? 'u-tab-item-active' : '']" :data-current="child.id" 
					@tap.stop="swichMenu(child.id)">
						<text class="u-line-1">{{child.name}}</text>
					</view>
				</block>
			</scroll-view>
			<scroll-view scroll-y class="right-box" @scrolltolower="scrollEvent">
				<u-tabs :list="sortList" :current="currentSort" @change="changeSort" bar-width="100" item-width="150" :is-scroll="true"></u-tabs>
				<view class="page-view">
					<view class="class-item">
						<view class="item-container">
							<navigator 
								class="thumb-box u-m-40" 
								v-for="(item1, index1) in goodsList" 
								:key="index1"
								:url="`/pages/goods/goodsInfo?id=${item1.id}`"
								>
								<image class="item-menu-image" :src="item1.cover_url" mode=""></image>
								<view class="item-menu-name">{{item1.title}}</view>
							</navigator>
						    <view class="u-flex-1 u-m-t-80">
							    <u-empty v-if="goodsList.length == 0" text="暂无该类商品~" mode="list"></u-empty>
						    </view>
						</view>
				 </view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>
	
<script>
	export default {
		data() {
			return {
				scrollTop: 0, //tab标题的滚动条位置
				oldScrollTop: 0,
				current: 0, // 预设当前项的值
				menuHeight: 0, // 左边菜单的高度
				menuItemHeight: 0, // 左边菜单item的高度
				menuItemPos: [],
				arr: [],
				scrollRightTop: 0, // 右边栏目scroll-view的滚动条高度
				timer: null, // 定时器
				keyword:'', //搜索词
				page:1, //请求页
				categories:[],
				goodsList:[],
				isLast:false ,//判断是否为最后一页
				currentSort:0,
				sortList:[{name:'销量'},{name:'推荐'},{name:'价格'},{name:'评论数'}],
			}
		},
		onLoad() {
			this.getData()
		},
		methods: {
			// 获取商品信息page
			async getData () {
				const params = {
					page:this.page,
					title:this.keyword,
					category_id:this.current //右侧展示列表的id
				}
				if(this.currentSort == 0) params.sales = 1
				if(this.currentSort == 1) params.recommend = 1
				if(this.currentSort == 2) params.price = 1
				if(this.currentSort == 3) params.comments_count = 1
				const res = await this.$u.api.getGoods(params)
				this.categories = res.categories
				this.goodsList = [...this.goodsList,...res.goods.data]
				this.isLast = res.goods.next_page_url ? false : true
				console.log(res)
			},
			// 搜索商品
			searchGoods () {
				this.initGooodsList()
			},
			// 清除商品搜索信息
			clearSeach () {
				this.keyword = ''
				this.initGooodsList()
			},
			// 右侧触底事件
			scrollEvent () {
				// 判断当前是否为最后一页
				if(this.isLast) {
					this.$u.toast('到底啦~')
					return
				}
				this.page += 1
				this.getData()
			},
			// 点击左边的栏目切换
			async swichMenu(index) {
				// index为二级菜单的id
				if(this.arr.length == 0) {
					await this.getMenuItemTop();
				}
				// 判断当前页ID是否与展示的一致
				if (index == this.current) return;
				this.current = index
				this.initGooodsList()
				this.scrollRightTop = this.oldScrollTop;
				this.$nextTick(function(){
					this.scrollRightTop = this.arr[index];
					this.current = index;
					this.leftMenuStatus(index);
				})
			},
			// 初始化goodlist
			initGooodsList () {
				this.page = 1
				this.goodsList = []
				this.getData()
			},
			// 切换tabbar标签
			changeSort(index) {
				this.currentSort = index
				this.goodsList = [] //改变分页时，情况原列表加载新产品信息
				this.getData()
			},
			// 获取一个目标元素的高度
			getElRect(elClass, dataVal) {
				new Promise((resolve, reject) => {
					const query = uni.createSelectorQuery().in(this);
					query.select('.' + elClass).fields({
						size: true
					}, res => {
						// 如果节点尚未生成，res值为null，循环调用执行
						if (!res) {
							setTimeout(() => {
								this.getElRect(elClass);
							}, 10);
							return;
						}
						this[dataVal] = res.height;
						resolve();
					}).exec();
				})
			},
			// 观测元素相交状态
			async observer() {
				this.categories.map((val, index) => {
					let observer = uni.createIntersectionObserver(this);
					// 检测右边scroll-view的id为itemxx的元素与right-box的相交状态
					// 如果跟.right-box底部相交，就动态设置左边栏目的活动状态
					observer.relativeTo('.right-box', {
						top: 0
					}).observe('#item' + index, res => {
						if (res.intersectionRatio > 0) {
							let id = res.id.substring(4);
							this.leftMenuStatus(id);
						}
					})
				})
			},
			// 设置左边菜单的滚动状态
			async leftMenuStatus(index) {
				this.current = index;
				// 如果为0，意味着尚未初始化
				if (this.menuHeight == 0 || this.menuItemHeight == 0) {
					await this.getElRect('menu-scroll-view', 'menuHeight');
					await this.getElRect('u-tab-item', 'menuItemHeight');
				}
				// 将菜单活动item垂直居中
				this.scrollTop = index * this.menuItemHeight + this.menuItemHeight / 2 - this.menuHeight / 2;
			},
			// 获取右边菜单每个item到顶部的距离
			getMenuItemTop() {
				new Promise(resolve => {
					let selectorQuery = uni.createSelectorQuery();
					selectorQuery.selectAll('.class-item').boundingClientRect((rects) => {
						// 如果节点尚未生成，rects值为[](因为用selectAll，所以返回的是数组)，循环调用执行
						if(!rects.length) {
							setTimeout(() => {
								this.getMenuItemTop();
							}, 10);
							return ;
						}
						rects.forEach((rect) => {
							// 这里减去rects[0].top，是因为第一项顶部可能不是贴到导航栏(比如有个搜索框的情况)
							this.arr.push(rect.top - rects[0].top);
							resolve();
						})
					}).exec()
				})
			},
			// 右边菜单滚动
			async rightScroll(e) {
				this.oldScrollTop = e.detail.scrollTop;
				if(this.arr.length == 0) {
					await this.getMenuItemTop();
				}
				if(this.timer) return ;
				if(!this.menuHeight) {
					await this.getElRect('menu-scroll-view', 'menuHeight');
				}
				setTimeout(() => { // 节流
					this.timer = null;
					// scrollHeight为右边菜单垂直中点位置
					let scrollHeight = e.detail.scrollTop + this.menuHeight / 2;
					for (let i = 0; i < this.arr.length; i++) {
						let height1 = this.arr[i];
						let height2 = this.arr[i + 1];
						// 如果不存在height2，意味着数据循环已经到了最后一个，设置左边菜单为最后一项即可
						if (!height2 || scrollHeight >= height1 && scrollHeight < height2) {
							this.leftMenuStatus(i);
							return ;
						}
					}
				}, 10)
			}
		}
	}
</script>

<style lang="scss" scoped>
